					<div class="burger_menu" @click="openMenu">
						<div class="burger"></div>
					</div>
					<nav>
						<div class="menu__list">
							<!-- <router-link class="menu__item" active-class="active" @click="closeMenu" to="/home">О компании</router-link> -->
							<router-link class="menu__item" active-class="active" @click="closeMenu" to="/my">Мои
								курсы</router-link>
							<router-link class="menu__item" active-class="active" @click="closeMenu"
								to="/news">Новости</router-link>
							<p class="menu__item" v-if="calcTabVisible" @click="showCalc">Калькулятор</p>
							<router-link v-if="user" class="menu__item" active-class="active" @click="closeMenu" to="/student">{{
								user
							}}</router-link>
							<button class="exit" @click="openModal"><img src="./style/icons/logout-24.png" alt=""></button>
						</div>
					</nav>
				</div>
			</div>
		</header>
		<router-view></router-view>
	</div>
	<check_logout @close="close" v-if="visibleModal"></check_logout>
	<calculator_modal @close="close" v-if="calcVisible"> </calculator_modal>
