<script>
import check_logout from '@/components/modal/checkLogout.vue'
import calculator_modal from '@/components/modal/calc_session.vue'
// import httpClient from '../services/http.service';
import authService from '../services/auth.service';

export default {
	name: "App",
	data: () => ({
		user: null,
		guest: true,
		admin: false,
		teacher: false,
		student: false,
		visibleModal: false,
		calcVisible: false,
		calcTabVisible: false
	}),
	components: {
		check_logout,
		calculator_modal
	},
	methods: {
		openMenu() {
			document.body.classList.add('close')
			document.body.style.overflow = 'hidden'
			let menuItems = document.getElementsByTagName('nav');
			for (let i = 0; i < menuItems.length; i++) {
				let menu = menuItems[i];
				menu.classList.add('opened')
			}
		},
		closeMenu() {
			document.body.classList.remove('close')
			let menuItems = document.getElementsByTagName('nav');
			for (let i = 0; i < menuItems.length; i++) {
				let menu = menuItems[i];
				menu.classList.remove('opened')
				document.body.style.overflow = 'auto'
			}
		},
		showCalc() {
			this.calcVisible = true
		},
		openModal() {
			this.closeMenu()
			document.body.style.overflow = 'hidden'
			this.visibleModal = true
		},
		close(value) {
			this.visibleModal = value
			this.calcVisible = value
			document.body.style.overflow = 'auto'
		},
		resetAll(value) {
			this.guest = false
			this.admin = false
			this.teacher = false
			this.student = false

			switch (value) {
				case 'admin':
					this.admin = true
					this.$router.push('/admin')
					break
				case 'teacher':
					this.teacher = true
					this.$router.push('/teacher')
					break
				case 'student':
					this.student = true
					this.$router.push('/student')
					break
				case 'guest':
					this.guest = true
					this.$router.push('/student')
					break
			}
		}
	},
	async created() {

		// Создаем новый элемент link с новой иконкой
		const newIconLink = document.createElement('link');
		newIconLink.rel = 'icon';
		newIconLink.type = 'image/png';
		newIconLink.href = '/logo.png';

		const existingIconLink = document.querySelector('link[rel="icon"]');

		if (existingIconLink) {
			document.head.removeChild(existingIconLink);
		}
		document.head.appendChild(newIconLink);



		if (localStorage.getItem('checked') === 'true') {
			this.calcTabVisible = true
		} else {
			this.calcTabVisible = false
		}

		this.user = localStorage.getItem('user')
		if (localStorage.getItem('role')) {
			let decryptedrole = localStorage.getItem('role')
			let role = authService.decryptText(decryptedrole)
			switch (role) {
				case 'admin':
					this.resetAll('admin')
					break
				case 'teacher':
					this.resetAll('teacher')
					break
				case 'student':
					this.resetAll('student')
					break
			}
		}
		else {
			this.resetAll('guest')
			authService.routerPush('home')
		}
	},
}
</script>
