<template>
	<div class="wrap">
		<div class="block town center">
			<div class="wrapper width-1200px">
				<div class="title">Программа курса {{ this.$route.params.course }}</div>
				<div class="left">
					<div class="theme" v-for="(item, key) in program" :key="key">
						{{ item }}
					</div>
					<p v-for="(item, key) in program" :key="key">
						{{ item }}
					</p>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import httpClient from '../../../services/http.service';
export default {
	name: 'info_page',
	data: () => ({
		program: []
	}),
	methods: {
		async getCourseData() {
			let id = this.$route.params.id

			await httpClient.post('courses/get-course-program-by-id?id=' + id).then(response => {
				this.program = response.data.programs.split('/')
			}).catch(e => {
				console.log(e);
			})
		}
	},
	mounted() {
		this.getCourseData()
	}
}
</script> 
<style scoped>
.left {
	text-align: left;
}

.left p {
	background: #000000b4;
	padding: 10px;
	width: 100%;
	margin: 10px 0px 10px 0px;
	font-size: 25px;
}
.no_bg {
	background: transparent;
}
.width-1200px {
	width: 1200px;
}
</style>