<template>
	<div class="wrap transparent">
		<div class="modal__body center">
			<div class="forms">
				<div class="form_body auth visible">
					<div class="title__form">Калькулятор сессии</div>
					<div class="close" @click="close"></div>
					<div class="error"> {{ error }}</div>
					<div class="item">
						<label for="vsk1">ВСК 1</label>
						<input type="number" :class="{ 'invalid': notNumber1 === false }" id="vsk1" v-model="vsk1" required>
					</div>
					<div class="item">
						<label for="vsk2">ВСК 2</label>
						<input type="number" :class="{ 'invalid': notNumber2 === false }" id="vsk2" v-model="vsk2" required>
					</div>
					<div class="item">
						<label for="total">Желаемое итоговое</label>
						<input type="number" :class="{ 'invalid': notNumber3 === false }" id="total" v-model="total" required>
					</div>
					<div class="item">
						<label for="#">{{ result }}</label>
					</div>
					<div class="item row">
						<button @click="resulting">Посчитать</button>
						<button @click="clear">Очистить</button>
					</div>
				</div>
			</div>
		</div>
	</div>
	<router-view></router-view>
</template>
<script>
export default {
	name: "calculator_modal",
	data() {
		return {
			error: '',
			result: null,
			total: null,
			vsk1: null,
			vsk2: null,
			notNumber1: null,
			notNumber2: null,
			notNumber3: null,
		}
	},
	emits: [
'close'
	],
	methods: {
		close() {
			this.$emit('close', false)
		},
		resulting() {
			if ((this.vsk1 + this.vsk2) > 200) {
				this.error = 'Два рубежных контроля не могут дать свыше 200 баллов'
				return
			}
			if ((this.vsk1 + this.vsk2) < 100) {
				this.error = 'Два рубежных контроля в сумме должны быть не менее 100'
				return
			}
			if (this.vsk1 == null || this.vsk2 == null || this.total == null) {
				this.error = 'Заполните все поля'
				return
			}
			if (this.vsk1 > 100 || this.vsk2 > 100) {
				this.error = 'Рубежный контроль должен быть меньше либо равен 100'
				return
			}
			if (typeof(this.vsk1) == String || typeof(this.vsk2) == String) {
				this.error = 'Рубежный контроль должен быть числом'
				return
			}
			if (this.vsk1 < 0 || this.vsk2 < 0) {
				this.error = 'Рубежный контроль должен быть больше нуля'
				return
			}
			let res = Math.ceil((this.total - 0.3 * ( this.vsk1 + this.vsk2))/0.4)

			if (res > 100) {
				this.error = 'На сессии нужно набрать свыше 100 баллов. Ошибка'
				return
			}
			if (this.total < 50) {
				this.error = 'Ошибка. Итоговый балл должен быть больше или равен 50'
				return
			}
			if (res <= 0) {
				this.error = 'Ошибка. Результат не может быть отрицательным'
				return
			}
			if (res < 50) {
				this.result = `Вам необходимо набрать всего лишь проходной балл (50)`
				return
			}
			if (res % 10 == 0 || res % 10 == 5) {
				this.result = `Вам необходимо набрать ${res} баллов`
				return
			}
			if (res % 10 == 1) {
				this.result = `Вам необходимо набрать ${res} балл`
				return
			}
			if (res % 10 < 5 && res % 10 > 1) {
				this.result = `Вам необходимо набрать ${res} балла`
			}	
		},
		clear() {
			this.vsk1 = this.vsk2 = this.total = null
			this.error = ''
		}
	},
	mounted() {
		document.body.style.overflow = 'hidden'
	}
}
</script>
<style scoped lang="scss">
$main_color: #d9730d;

* {
	border: 0;
	background: 0;
	outline: none;
	text-shadow: none;
}

.button {
	cursor: pointer;
}

.z-index {
	position: absolute;
	top: 0;
	left: 0;
	min-width: 100%;
	min-height: 100vh;
	overflow: hidden;
	z-index: 3;
	background: url('../../style/image/town.jpg');
	-webkit-background-size: cover;
	background-size: cover;
	background-attachment: fixed;
}

.modal__body {
	min-height: 100%;
	min-width: 100vw;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 8;
}

.center {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.forms {
	width: 1000px;
	height: 100vh;
	text-align: center;
	background: rgba(0, 0, 0, 0.767);
	position: relative;
}

@media screen and (max-width: 1017px) {
	.forms {
		width: 800px;
	}
}

@media screen and (max-width: 800px) {
	.forms {
		width: 700px;
	}
}

@media screen and (max-width: 700px) {
	.forms {
		width: 100%;
	}
}

.auth {
	width: 100%;
	padding: 0 100px;
	opacity: 0;
	position: absolute;
	top: 50%;
	left: 0;
	transform: translateY(-50%);
	transition: all .4s ease-in;
	pointer-events: none;
}

.regis {
	width: 100%;
	padding: 0 100px;
	opacity: 0;
	position: absolute;
	top: 50%;
	left: 0;
	transform: translateY(-50%);
	transition: all .4s ease-in;
	pointer-events: none;
}

input {
	border: 2px solid #fff;
	padding: 10px;
	margin: 15px 0px 7px 0px;
	width: 100%;
	border-radius: 10px;
}

.visible {
	opacity: 1;
	pointer-events: all;
}

.title__form {
	font-size: 30px;
	margin: 0px 0px 45px 0px;
}


.btn {
	width: 100px;
	border: 1px solid #fff;
	padding: 20px;
	text-align: center;
	color: #fff;
	transition: all .4s ease-in;
	border-radius: 7px;
}

.btn:hover {
	cursor: pointer;
	background: $main_color;
	border: 1px solid $main_color;
}

.error {
	color: #f00;
}

.item {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin: 0px 0px 25px 0px;
}

::placeholder {
	text-shadow: none;
	color: #9b9b9b;
}

.transparent {
	background: transparent;
}

.row {
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	padding: 0 15px;
}
.close::after {
	content: '✖';
	position: absolute;
	font-size: 35px;
	top: -6px;
	right: 150px;
	color: #f00;
	cursor: pointer;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
	-webkit-appearance: none;
	margin: 0;
}
</style>