<template>
	<div class="block town z-index">
	</div>
	<div class="wrap">
		<div class="modal__body center">
			<div class="forms">
				<div class="form_body auth" :class="{ visible: !is_active }">
					<div class="title__form">Авторизация</div>
					<div class="close" @click="close"></div>
					<div class="error" v-if="!correct">{{ error }}</div>
					<div class="correct" v-if="correct">{{ error }}</div>
					<div class="item">
						<label for="username">Имя пользователя:</label>
						<input type="text" :class="{ 'invalid': loginCorrect === false }" id="username"
							placeholder="Введите логин, почту или телефон" v-model="login" required>
					</div>
					<div class="item">
						<label for="password">Пароль:</label>
						<div class="password-input">
							<input :type="showPassword ? 'text' : 'password'" :class="{ 'invalid': passwordCorrect === false }"
								v-model="password" id="password1" placeholder="Введите пароль..." required />
							<button class="no_border" @click="togglePasswordVisibility">
								<img src="../../style//icons/invisible-24.png" v-if="!showPassword" alt="">
								<img src="../../style//icons/visible-24.png" v-if="showPassword" alt="">
							</button>
						</div>
					</div>
					<div class="item row">
						<div>Нет аккаунта?</div> <button class="button" @click="toggle">Регистрация</button>
					</div>
					<div class="item">
						<input type="submit" @click="auth" class="btn" value="Войти">
					</div>
				</div>
				<div class="form_body regis" :class="{ visible: is_active }">
					<div class="title__form">Регистрация</div>
					<div class="close" @click="close"></div>
					<div class="error" v-if="!correct">{{ error }}</div>
					<div class="correct" v-if="correct">{{ error }}</div>
					<div class="item">
						<label for="name">Фамилия Имя:</label>
						<input type="text" id="name" placeholder="Иванов Никита" v-model="name"
							:class="{ 'invalid': nameCorrect === false }">
					</div>
					<div class="item">
						<label for="username1">Логин:</label>
						<input type="text" id="username1" :class="{ 'invalid': loginCorrect === false }"
							placeholder="Введите логин..." v-model="login">
					</div>
					<div class="item">
						<label for="password1">Пароль:</label>
						<div class="password-input">
							<input :type="showPassword ? 'text' : 'password'"
								:class="{ 'invalid': passwordsAreTheSame === false || passwordCorrect === false }"
								v-model="password" id="password1" placeholder="Введите пароль..." />
							<button class="no_border" @click="togglePasswordVisibility">
								<img src="../../style//icons/invisible-24.png" v-if="!showPassword" alt="">
								<img src="../../style//icons/visible-24.png" v-if="showPassword" alt="">
							</button>
						</div>
					</div>
					<div class="item">
						<label for="password2">Повторите пароль:</label>
						<div class="password-input">
							<input :type="showPassword1 ? 'text' : 'password'" v-model="repassword" id="password2"
								placeholder="Введите пароль..."
								:class="{ 'invalid': passwordsAreTheSame === false || passwordCorrect === false }" />
							<button class="no_border" @click="togglePasswordVisibility1">
								<img src="../../style//icons/invisible-24.png" v-if="!showPassword1" alt="">
								<img src="../../style//icons/visible-24.png" v-if="showPassword1" alt="">
							</button>
						</div>
					</div>
					<div class="item row">
						<div>Уже есть аккаунт?</div><button class="button" @click="toggle">Вход</button>
					</div>
					<div class="item">
						<input type="submit" class="btn" @click="regis" value="Зарегистрироваться">
					</div>
				</div>
			</div>
		</div>
	</div>
	<router-view></router-view>
</template>
<script>
import authService from '../../../services/auth.service'
export default {
	name: "login_page",
	data() {
		return {
			is_active: false,
			error: '',
			form: {

			},
			login: '',
			password: '',
			repassword: '',
			name: '',
			showPassword: false,
			showPassword1: false,
			correct: false,
			passwordCorrect: null,
			loginCorrect: null,
			passwordsAreTheSame: null,
			nameCorrect: null
		}
	},
	methods: {
		errorClear() {
			this.loginCorrect = null
			this.passwordCorrect = null
			this.passwordsAreTheSame = null
			this.nameCorrect = null
			this.error = ''
			this.correct = false
		},
		showEmpty() {
			this.error = 'Заполните обязательные поля'
		},
		close() {
			this.errorClear()
			this.$router.push('/home')
			document.body.style.overflow = 'auto'
		},
		togglePasswordVisibility() {
			this.showPassword = !this.showPassword
		},
		togglePasswordVisibility1() {
			this.showPassword1 = !this.showPassword1
		},
		toggle() {
			this.errorClear()
			this.is_active = !this.is_active
			if (this.is_active !== true) {
				document.title = 'Авторизация'
			} else {
				document.title = 'Регистрация'
			}
			
		},
		async auth() {
			this.errorClear()
			if (this.login == '' && this.password == '') {
				this.error = 'Заполните обязательные поля'
				this.loginCorrect = false
				this.passwordCorrect = false
				return
			}

			if (!this.login) {
				this.error = 'Логин не может быть пустым'
				this.loginCorrect = false
				return
			}

			if (!this.password) {
				this.error = 'Пароль не может быть пустым'
				this.passwordCorrect = false
				return
			}

			this.form.login = this.login
			this.form.password = authService.encryptText(this.password)
			const { success} = await authService.login(this.form)
			if (success) {
				this.error = ''
				this.$router.go(0)
			}
			else {
				this.correct = false
				this.error = 'Неверно имя пользователя или пароль'
			}
		},


		async regis() {

			this.errorClear()

			if (this.name == '') {
				this.showEmpty()
				this.nameCorrect = false
				return
			}
			if (this.login == '') {
				this.showEmpty()
				this.loginCorrect = false
				return
			}
			if (this.password == '') {
				this.showEmpty()
				this.passwordCorrect = false
				return
			}
			if (this.repassword === this.password) {
				this.errorClear()
				this.form.login = this.login
				this.form.password = authService.encryptText(this.password)
				this.form.fullname = this.name
				const { success, errors } = await authService.regis(this.form)
				if (!success) {
					this.error = 'Что-то пошло  не так. Повторите попытку'
					console.log(errors);
				}
				else {
					this.errorClear()
					this.correct = true
					this.error = 'Регистрация успешна, можете войти'
					this.is_active = false
				}
			}
			else {
				this.passwordsAreTheSame = false
				this.error = 'Пароли не совпадают'
			}
		}
	},
	mounted() {
		this.errorClear()
		document.body.style.overflow = 'hidden'
	}
}
</script>
<style scoped lang="scss">
$main_color: #d9730d;

* {
	border: 0;
	background: 0;
	outline: none;
	text-shadow: none;
}

.button {
	cursor: pointer;
}

.z-index {
	position: absolute;
	top: 0;
	left: 0;
	min-width: 100%;
	min-height: 100vh;
	overflow: hidden;
	z-index: 3;
	background: url('../../style/image/town.jpg');
	-webkit-background-size: cover;
	background-size: cover;
	background-attachment: fixed;
}

.modal__body {
	min-height: 100%;
	min-width: 100vw;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 8;
}

.center {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.forms {
	width: 1000px;
	height: 100vh;
	text-align: center;
	background: rgba(0, 0, 0, 0.767);
	position: relative;
}

@media screen and (max-width: 1017px) {
	.forms {
		width: 800px;
	}
}

@media screen and (max-width: 800px) {
	.forms {
		width: 700px;
	}
}

@media screen and (max-width: 700px) {
	.forms {
		width: 100%;
	}
}

.auth {
	width: 100%;
	padding: 0 100px;
	opacity: 0;
	position: absolute;
	top: 50%;
	left: 0;
	transform: translateY(-50%);
	transition: all .4s ease-in;
	pointer-events: none;
}

.regis {
	width: 100%;
	padding: 0 100px;
	opacity: 0;
	position: absolute;
	top: 50%;
	left: 0;
	transform: translateY(-50%);
	transition: all .4s ease-in;
	pointer-events: none;
}

input {
	border: 2px solid #fff;
	padding: 10px;
	margin: 15px 0px 7px 0px;
	width: 100%;
	border-radius: 10px;
}

.visible {
	opacity: 1;
	pointer-events: all;
}

.title__form {
	font-size: 30px;
	margin: 0px 0px 45px 0px;
}

.close::after {
	content: '✖';
	position: absolute;
	font-size: 35px;
	top: -6px;
	right: 150px;
	color: #f00;
	cursor: pointer;
}

.btn {
	width: 100%;
	border: 1px solid #fff;
	text-align: center;
	color: #fff;
	transition: all .4s ease-in;
}

.btn:hover {
	cursor: pointer;
	background: $main_color;
	border: 1px solid $main_color;
}

.error {
	color: #f00;
}

.correct {
	color: #0f0;
}

.item {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin: 0px 0px 15px 0px;
}

::placeholder {
	text-shadow: none;
	color: #9b9b9b;
}

.row {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 0 15px;
}

.invalid {
	border-color: red;
}

.valid {
	border-color: green;
}

.password-input {
	width: 100%;
	position: relative;
	height: 48px;
	margin: 0px 0px 15px 0px;
}

.password-input button {
	position: absolute;
	top: 26px;
	right: 15px;
	width: 24px;
	height: 24px;
}

.invalid {
	border-color: #f00;
}
</style>