import httpClient from "./http.service"
import router from "../router"


const authService = {
	currentUser: null,
	user: null,
	isValidAuthKey: null,
	isLoggedIn() {
		return !!localStorage.getItem('authToken')
	},
	setStore(store) {
		this.$store = store;
	},
	getToken() {
		return localStorage.getItem('authToken')
	},
	routerPush(name) {
		return router.push('/' + name)
	},
	generateRandomChars(length) {
		const chars = 'abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ1234567890./&?#$!*@';
		let randomChars = '';

		for (let i = 0; i < length; i++) {
			const randomIndex = Math.floor(Math.random() * chars.length);
			randomChars += chars.charAt(randomIndex);
		}
		return randomChars;
	},
	encryptText(text) {
		let encryptedText = '';

		for (let i = 0; i < text.length; i++) {
			let additionalChars = this.generateRandomChars(10);
			encryptedText += text.charAt(i) + additionalChars;
		}
		return encryptedText;
	},
	decryptText(encryptedText) {
		if (!encryptedText) {
			return
		}
		const additionalCharsLength = 10;
		let role = '';
		for (let i = 0; i < encryptedText.length; i += additionalCharsLength + 1) {
			role += encryptedText.charAt(i);
		}
		return role;
	},
	async login(formData) {
		try {
			const { status, data } = await httpClient.post('/login', formData)
			if (status === 200) {
				localStorage.setItem('authToken', data.user.access_token)
				let role = Object.keys(data.roles)[0]
				localStorage.setItem('user', data.user.fullname)
				localStorage.setItem('role', this.encryptText(role))
				this.setupUserData(data)
				return { success: true }
			}
		} catch (e) {
			return {
				success: false,
				errors: e?.response?.data?.errors || {}
			}
		}
		return { success: false, errors: {} }
	},
	async regis(formData) {
		try {
			const { status} = await httpClient.post('/sign-up', formData)
			if (status === 200) {
				return { success: true }
			}
		} catch (e) {
			return {
				success: false,
				errors: e?.response?.data?.errors || {}
			}
		}
		return { success: false, errors: {} }
	},
	async logout() {
		const { status } = await httpClient.post('/logout')
		status === 200 ? console.log('success') : console.log('error')
		localStorage.clear()
		this.currentUser = null
		this.isValidAuthKey = null
		router.go(0)
	},
	async getUserData() {
		const auth_key = localStorage.getItem('authToken')
		if (!this.currentUser && auth_key) {
			try {
				const { status, data } = await httpClient.get(`user/get-user-by-auth-key?auth_key=${auth_key}`)
				if (status === 200) {
					this.setupUserData(data)
					return
				}
			} catch (e) {
				this.isValidAuthKey = false
				return e
			}
		}
		this.isValidAuthKey = false
	},

	setupUserData(data) {
		this.isValidAuthKey = true
		this.currentUser = data.user
		this.currentUser = {
			roles: Object.keys(data.roles)
		}
	}

}

export default authService