<template>
	<div class="wrap">
		<div class="block town center">
			<div class="profile__body container">
				<div class="grid">
					<div class="child profile">

					</div>
					<div class="child">
						<table class="table">
							<thead>
								<tr>
									<td>
										<button v-on:click="decrease">
											{{ prev }}
										</button>
									</td>
									<td colspan="5">{{ monthes[month] }} {{ year }}</td>
									<td>
										<button v-on:click="increase">{{ next }}</button>
									</td>
								</tr>
								<tr>
									<td v-for="(day, key) in dayChange" :key="key">{{ day }}</td>
								</tr>
							</thead>
							<tbody>
								<tr class="week" v-for="(week, key) in calendar()" :key="key">
									<td class="days" @click="showSelectedDeadlines(day.index)" :value="day.index"
										v-for="(day, key) in week" :key="key" :style="{
											color: day.weekend ? 'red' : 'black',
										}" :class="{ 'current': day.current, 'empty': !day.index }">
										{{ day.index }}
									</td>
								</tr>
							</tbody>
						</table>
					</div>
					<div class="functionallity">
						<div class="box_item">
							<div class="head center">Полезное</div>
							<ul>
								<li class="list" @click="toggleFunc">
									<div class="checkbox" :class="{ 'filed': checked }">
									</div>
									Включить калькулятор сессии
								</li>
							</ul>

						</div>
					</div>
					<div class="other">
						<div class="box_item">

						</div>
					</div>
					<div class="deadlines">
						<div class="box_item">
							<p class="date center">{{ date }}</p>
							<p v-if="!deadline"> На выбранной дате нет дедлайнов </p>
							<p v-if="deadline">{{ task }}</p>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
	<div class="calc">
		
	</div>
</template>
 
<script>
export default {
	name: "user_page",
	data() {
		return {
			prev: "<",
			next: ">",
			deadline: null,
			checked: false,
			task: null,
			date: '',
			month: 6,
			year: 2023,
			day: 0,
			dFirstMonth: 1,
			selectedDay: 0,
			weekend: ["Сб", "Вс"],
			monthes: [
				"Январь",
				"Февраль",
				"Март",
				"Апрель",
				"Май",
				"Июнь",
				"Июль",
				"Август",
				"Сентябрь",
				"Октябрь",
				"Ноябрь",
				"Декабрь",
			],
		};
	},
	computed: {
		dayChange: function () {
			if (this.dFirstMonth === 0) {
				return ["Вс", "Пн", "Вт", "Ср", "Чт", "Пт", "Сб"];
			} else {
				return ["Пн", "Вт", "Ср", "Чт", "Пт", "Сб", "Вс"];
			}
		},
	},
	methods: {
		showSelectedDeadlines(value) {
			this.selectedDay = value; // Сохраняем выбранный день
			let day = value <= 9 ? '0' + value : String(value);
			let month = this.month + 1 <= 9 ? '0' + (this.month + 1) : String(this.month + 1);
			this.date = day + '.' + month + '.' + this.year;
		},
		showToday() {
			let date = new Date();
			const day = String(date.getDate()).padStart(2, '0');
			const month = String(date.getMonth() + 1).padStart(2, '0');
			const year = date.getFullYear();
			return this.date = `${day}.${month}.${year}`;
		},
		toggleFunc() {
			if (this.checked == false) {
				this.checked = true
				localStorage.setItem('checked', true)
			}
			else {
				this.checked = false
				localStorage.setItem('checked', false)
			}
			this.$router.go(0)
		},
		increase: function () {
			this.month++;
			if (this.month > 11) {
				this.month = 0;
				this.year++;
			}
		},
		decrease: function () {
			this.month--;
			if (this.month < 0) {
				this.month = 11;
				this.year--;
			}
		},
		calendar: function () {
			let day = 1;
			let today = new Date();
			let currentMonth = today.getMonth();
			let currentYear = today.getFullYear();
			let selectedMonth = this.month;
			let selectedYear = this.year;
			let firstDay = new Date(selectedYear, selectedMonth, 1).getDay();
			let daysInMonth = new Date(selectedYear, selectedMonth + 1, 0).getDate();
			let calendar = [];
			let numWeeks = Math.ceil((daysInMonth + firstDay) / 7);

			for (let i = 0; i < numWeeks; i++) {
				let week = [];
				for (let j = 0; j < 7; j++) {
					if (i === 0 && j < firstDay) {
						week.push({
							index: "",
							current: false,
							weekend: false,
						});
					} else if (day > daysInMonth) {
						week.push({
							index: "",
							current: false,
							weekend: false,
						});
					} else {
						week.push({
							index: day,
							current: day === (this.selectedDay === 0 ? today.getDate() : this.selectedDay) && selectedMonth === currentMonth && selectedYear === currentYear,
							weekend: this.weekend.includes(this.dayChange[j]),
						});
						day++;
					}
				}
				calendar.push(week);
			}
			return calendar;
		},
	},
	mounted() {
		if (localStorage.getItem('checked') == 'true') {
			this.checked = true
		}
		else {
			this.checked = false
		}
		this.showToday()
	}
};
</script>
<style scoped>
.grid {
	display: grid;
	width: 100%;
	grid-template-areas:
		'profile profile calendar'
		'functionallity other deadline'
	;
	grid-template-rows: 1fr 0.5fr;
	grid-template-columns: 1fr 1fr 1fr;
	gap: 10px;
}

.functionallity {
	grid-area: functionallity;
	background: rgba(168, 168, 168, 0.349);
}

.other {
	grid-area: other;
	background: rgba(168, 168, 168, 0.349);
}

.deadlines {
	grid-area: deadline;
	background: rgba(168, 168, 168, 0.349);
}

.profile {
	grid-area: profile;
	background: rgba(168, 168, 168, 0.349);
}

.table {
	grid-area: calendar;
	height: 484px;
	border-collapse: collapse;
	min-width: 100%;
}

.table * {
	text-shadow: none;
	font-size: 20px;
}

td {
	/* border: 1px solid #ffffff; */
	padding: 17px;
	text-align: center;
	color: #ffffff;
}

thead tr {
	background: #ff8800;
	color: #000;
}

tbody {
	background: rgba(168, 168, 168, 0.349);
	color: #000;
}

.days {
	background: #ffffff;
	transition: filter .1s ease-in-out;
}

.days:hover {
	filter: brightness(.8);
	cursor: pointer;
}
.checkbox {
	width: 20px;
	height: 20px;
	border: 2px solid #ffbc59;
	margin: 0px 15px 0px 0px;
	transition: all .3s ease-in-out;
	border-radius: 4px;
}
.list {
	margin: 10px 0;
	display: flex;
	align-items: center;
	cursor: pointer;
}
.filed {
	background: #ffbc59;
}
.box_item {
	width: 100%;
	height: 100%;
	text-align: left;
	padding: 20px;
}

button {
	background: transparent;
	border: none;
	color: white;
	padding: 10px 20px;
	text-align: center;
	text-decoration: none;
	display: inline-block;
	font-size: 16px;
	margin: 4px 2px;
	cursor: pointer;
}

.current {
	background: #ff8800;
}

.empty {
	background: transparent;
	border: 0;
}

.empty:hover {
	background: transparent;
	border: 0;
	cursor: default;
}

@media screen and (max-width: 1220px) {
	.grid {
		grid-template-columns: 1fr 1fr;
		grid-template-rows: repeat(2, 1fr);
	}
}

@media screen and (max-width: 1024px) {
	.grid {
		grid-template-areas:
			'calendar'
			'deadline'
			'profile'
			'functionallity'
			'other'
		;
		grid-template-columns: 1fr;
		gap: 5px;
		grid-template-rows: repeat(5, 1fr);
	}
}
</style>
 