<template>
	<div class="block town z-index">
	</div>
	<div class="wrap">
		<div class="modal__body center">
			<div class="forms">
				<div class="form_body auth visible">
					<div class="title__form">Вы уверены что хотите выйти?</div>
					<div class="item row">
						<div class="btn yes" @click="logout">Да</div>
						<div class="btn no" @click="close">Нет</div>
					</div>
				</div>
			</div>
		</div>
	</div>
	<router-view></router-view>
</template>
<script>
import authService from '../../../services/auth.service'
export default {
	name: "check_logout",
	data() {
		return {
			error: '',
		}
	},
	methods: {
		close() {
			this.$emit('close', false)
		},
		async logout() {
			document.body.style.overflow = 'auto'
			await authService.logout()
		},
	},
	mounted() {
		document.body.style.overflow = 'hidden'
	}
}
</script>
