<script>
import httpClient from '../../../services/http.service';
import { mapGetters, mapActions } from 'vuex';
export default {
	name: 'home_page',
	data() {
		return {
			reviews: [],
			review: {
				name: '',
				content: '',
				rating: ''
			},
			selectedRating: 0,
			currentSlideIndex: 0,
			intervalId: null,
			formData: {
				name: '',
				phone: '',
				question: ''
			},
			file: null,
			num: 0,
			colorMap: [],
			visibleAlert: false,
			msg: '',
			error: false,
			succsess: false,
			questions: [
				{
					text: 'Вопрос номер 1',
					answer: 'Далеко-далеко за словесными горами в стране гласных и согласных живут рыбные тексты. Если его над решила! Ты снова составитель то ее. Переписали вдали журчит вопроса рекламных заманивший? Это журчит семь заманивший ручеек языкового снова несколько. Свой грамматики предупреждал злых океана строчка, приставка всемогущая переписывается свое рот они, напоивший встретил домах жаренные lorem.',
					showAnswer: false
				},
				{
					text: 'Вопрос номер 2',
					answer: 'Далеко-далеко за словесными горами в стране гласных и согласных живут рыбные тексты. Если его над решила! Ты снова составитель то ее. Переписали вдали журчит вопроса рекламных заманивший? Это журчит семь заманивший ручеек языкового снова несколько. Свой грамматики предупреждал злых океана строчка, приставка всемогущая переписывается свое рот они, напоивший встретил домах жаренные lorem.',
					showAnswer: false
				},
				{
					text: 'Вопрос номер 3',
					answer: 'Далеко-далеко за словесными горами в стране гласных и согласных живут рыбные тексты. Если его над решила! Ты снова составитель то ее. Переписали вдали журчит вопроса рекламных заманивший? Это журчит семь заманивший ручеек языкового снова несколько. Свой грамматики предупреждал злых океана строчка, приставка всемогущая переписывается свое рот они, напоивший встретил домах жаренные lorem.',
					showAnswer: false
				},
				// Добавьте другие вопросы и ответы в массив 'questions' по аналогии
			],
			searchTerm: '',
			num2: 0,
			num3: 0
		};
	},
	computed: {
		filteredQuestions() {
			const searchTerm = this.searchTerm.toLowerCase();
			return this.questions.filter(question => question.text.toLowerCase().includes(searchTerm));
		},
		...mapGetters(['getNumbers']),
	},
	methods: {
		...mapActions(['showNumberAnim']),
		onFileChange(event) {
			this.file = event.target.files[0];
		},
		toggleAnswer(index) {
			this.questions[index].showAnswer = !this.questions[index].showAnswer;
		},
		offset(el) {
			const rect = el.getBoundingClientRect(),
				scrollLeft = window.scrollX || document.documentElement.scrollLeft,
				scrollTop = window.scrollY || document.documentElement.scrollTop
			return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
		},
		sendReview(event) {
			event.preventDefault()
			this.closeAlert()
			if (this.file) {
				const formData = new FormData();
				formData.append('file', this.file);
				httpClient.post('/upload', formData)
					.then(response => {
						// Обработка успешной загрузки файла
						if (response.data.succsess == true) {
							let succsess_msg = 'Файл успешно загружен'
							this.showAlert(succsess_msg, true)
						} else {
							let error_msg = 'Ошибка загрузки файла. Убедитесь что загружаете файлы типа "png", "jpeg" или "jpg"'
							this.showAlert(error_msg, false)
						}
					})
					.catch(error => {
						console.log(error);
						let error_msg = 'Ошибка загрузки файла. Попробуйте позже'
						this.showAlert(error_msg, false)
					});
			}
			else {
				alert('Файл пуст')
			}
		},
		showAlert(msg, value) {
			if (value === false) {
				this.error = true
			} else {
				this.succsess = true
			}
			this.visibleAlert = true
			this.msg = msg
		},
		closeAlert() {
			this.visibleAlert = false
			this.succsess = false
			this.error = false
		},
		selectRating(rating) {
			this.review.rating = rating.toString();
			this.selectedRating = rating;
		},
		getColorCode(name) {
			if (this.colorMap[name]) {
				return this.colorMap[name]; // возвращаем ранее сохраненный цвет
			} else {
				var color = this.getRandomPastelColor();
				this.colorMap[name] = color; // сохраняем сгенерированный цвет
				return color;
			}
		},
		// Генерация цвета в пастельных тонах рандомно
		getRandomPastelColor() {
			var hue = Math.floor(Math.random() * 360);
			var pastel = 'hsl(' + hue + ', 30%, 80%)';
			return pastel;
		},
		getInitials(name) {
			return name.charAt(0).toUpperCase();
		},
		async getReviews() {
			await httpClient.get('api/get-reviews')
				.then(response => {
					this.reviews = response.data;
				})
				.catch(error => {
					console.error('Ошибка при получении отзывов:', error);
				});
		},
		prevSlide() {
			this.currentSlideIndex = (this.currentSlideIndex - 1 + this.reviews.length) % this.reviews.length;
		},
		nextSlide() {
			this.currentSlideIndex = (this.currentSlideIndex + 1) % this.reviews.length;
		},
		animOnScroll(animItems) {
			for (let index = 0; index < animItems.length; index++) {
				const animItem = animItems[index];
				const animItemHeight = animItem.offsetHeight;
				const animItemOffset = this.offset(animItem).top;
				const animStart = 4;

				let animItemPoint = window.innerHeight - animItemHeight / animStart;

				if (animItemHeight > window.innerHeight) {
					animItemPoint = window.innerHeight - window.innerHeight / animStart;
				}

				if (
					scrollY > animItemOffset - animItemPoint &&
					scrollY < animItemOffset + animItemHeight
				) {
					setTimeout(() => {
						animItem.classList.add('_active');
					}, 150);
				} else {
					if (!animItem.classList.contains('_no-repeat-anim')) {
						animItem.classList.remove('_active');
					}
				}
			}
		}

	},
	mounted() {
		const animItems = document.querySelectorAll('._anim-items')

		if (animItems.length > 0) {
			window.addEventListener('scroll', () => {
				this.animOnScroll(animItems); // Передаем список элементов в метод animOnScroll
			});
		}
		this.getReviews()
		this.currentSlide = this.reviews[0]
	}
}
</script>
